@import "_reset.css";
@import url("https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap");

a {
  color: #cc0000;
}

code {
  font-size: 1rem;
}

body {
  margin: 0 8px;
  font-family: Merriweather, serif;
  background-color: #fafaef;
  line-height: 175%;
}

blockquote {
  border-left: 1px solid #ccc;
  color: #555;
  font-style: italic;
  margin-left: 1rem;
  padding-left: 1rem;
}

ul {
  padding-left: 1.5rem;
}

body > .page-content {
  max-width: 42rem;
  margin: 2rem auto 0;
}

.posts-list {
  ul {
    padding-left: 1rem;
  }
}

.site-header {
  a {
    color: #000;
    text-decoration: none;
  }
}

.site-header-content {
  align-items: center;
  display: flex;

  > * + * {
    margin-left: 2rem;
  }

  .site-header-content__avatar {
    border-radius: 50%;
    outline: #ffffff solid 3px;
    width: 120px;
  }

  .site-header-content__title {
    font-size: 2rem;
    font-weight: 500;
    line-height: 2.3rem;
  }

  .site-header-content__subtitle {
    font-style: italic;
    line-height: 1.5rem;
  }
}

.site-nav {
  ul {
    list-style-type: none;
    display: flex;
    padding: 0;
  }

  ul > li + li {
    margin-left: 1rem;
  }
}

.site-footer {
  margin: 2rem 0;
  color: #3c3c3c;
  font-style: italic;

  .copyright {
    position: relative;
    top: 2px;
  }
}

.published-at {
  margin-block-start: 0;
  font-size: 0.85rem;
}
